<template>
    <div class="page-container">
        <div class="member-container">
            <div class="member-list">
                <div class="member-list-header">
                    <div class="member-title" style="padding:3px 0 0 0;">약관 목록</div>
                </div>
                <div class="member-list-body">
                    <ul v-for="(item,index) in memberList" class="main-member">
                        <li class="main-li">
                            <div :class="'main-data '+item.active">
                                <div class="gname" @click="choiceMember(item.id)">
                                    {{ item.name }}
                                    <font-awesome-icon v-if="item.use=='yes'" icon="fa-solid fa-eye"/>
                                    <font-awesome-icon v-else icon="fa-solid fa-eye-slash"/>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="member-regist">
                <div v-if="agreeId">
                    <div class="member-title">{{ memberRegistTitle }}</div>
                    <form novalidate @submit.prevent="validate">
                        <ul class="table-ul">
                            <li>
                                <div class="label">
                                    사용여부
                                </div>
                                <div class="label-input">
                                    <div class="radio-box">
                                        <v-radio-group
                                            v-model="use"
                                            dense
                                            row
                                        >
                                            <v-radio
                                                label="사용"
                                                value="yes"
                                            ></v-radio>
                                            <v-radio
                                                label="사용안함"
                                                value="no"
                                            ></v-radio>
                                        </v-radio-group>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <div class="quill-content-box" style="width:900px;">
                            <quill-editor ref="myQuillEditor" :content="content"
                                          :options="editorOption"
                                          @change="onEditorChange($event)">
                            </quill-editor>

                        </div>
                        <div class="button-row">
                            <v-btn class="submit-button" type="submit" style="width:250px;">{{ memberRegistTitle }} 저장
                            </v-btn>
                        </div>
                    </form>
                </div>
            </div>
        </div>


    </div>
</template>


<script>
import apiService from '@/config/admin/apiService'
import {
    API_ADMIN_UPDATE_SETTING_AGREE,
    API_ADMIN_GET_SETTING_AGREE
} from '@/config/admin/urls'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import {quillEditor} from 'vue-quill-editor'
import JQ from 'jquery'

export default {
    components: {
        quillEditor
    },
    data() {
        return {
            memberList: [
                {name: '이용약관', use: 'yes', id: 'use', content: ''},
                {name: '개인정보처리방침', use: 'yes', id: 'privacy', content: ''},
                {name: '개인정보 수집/동의항목', use: 'yes', id: 'collect', content: ''},
                {name: '탈퇴안내', use: 'yes', id: 'out'},
            ],
            agrees:{},
            memberRegistTitle: '',
            agreeId: '',
            name: '',
            content: '',
            use: 'yes',
            serverFlag: false,
            editorOption: {
                placeholder: '내용을 입력하세요',
                theme: 'snow',
                modules: {
                    toolbar: {
                        container: [
                            [{'header': 1}, {'header': 2}],               // custom button values
                            ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
                            ['blockquote', 'code-block'],
                            [{'list': 'ordered'}, {'list': 'bullet'}],
                            [{'indent': '-1'}, {'indent': '+1'}],          // outdent/indent
                            [{'direction': 'rtl'}],                         // text direction
                            [{'size': ['small', false, 'large', 'huge']}],  // custom dropdown
                            [{'header': [1, 2, 3, 4, 5, 6, false]}],
                            [{'color': []}, {'background': []}],          // dropdown with defaults from theme
                            //[{ 'font': [] }],
                            [{'align': []}],
                            ['clean']
                        ]
                    }
                }
            },
        };
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            if (this.serverFlag) return;
            this.serverFlag = true;
            this.agrees = {};
            apiService.post(API_ADMIN_GET_SETTING_AGREE, {}, (res) => {
                this.serverFlag = false;
                let agrees = {};
                if(res.setting && res.setting.agree) {
                    agrees = JSON.parse(res.setting.agree);
                }
                for (let item of this.memberList) {
                    if(agrees[item.id]) {
                        item.use = agrees[item.id].use;
                        item.content = agrees[item.id].content;
                        this.agrees[item.id] = item;
                    } else {
                        this.agrees[item.id] = item;
                    }
                }
                this.choiceMember(this.memberList[0].id)

            })
        },
        choiceMember(id) {

            let activeInfo = {};
            for (let item of this.memberList) {
                if (item.id == id) {
                    item.active = 'active';
                    activeInfo = item;
                } else {
                    item.active = '';
                }
            }
            this.agreeId = activeInfo.id;
            this.memberRegistTitle = activeInfo.name;
            this.content = activeInfo.content;
            this.use = activeInfo.use;

        },
        onEditorChange({quill, html, text}) {

            // console.log('editor change!', quill, html, text)
            this.content = html
        },
        validate() {
            if(!this.agreeId)return;
            this.agrees[this.agreeId].use = this.use;
            this.agrees[this.agreeId].content = this.content;
            if (!this.content) {
                let params = {
                    message: '내용을 입력하세요'
                }
                this.$eventBus.$emit('modalOpen', params);
                return;
            }
            let frm = {
                agree:JSON.stringify(this.agrees),
            }
            if (this.serverFlag) return;
            this.serverFlag = true;
            this.$eventBus.$emit('overlay', 'open');
            apiService.post(API_ADMIN_UPDATE_SETTING_AGREE,frm, (res) => {
                this.serverFlag = false;
                this.$eventBus.$emit('overlay', 'hide');
                if (res.data) {
                    for (let item of this.memberList) {
                        if (item.id == this.agreeId) {
                            item.content = this.content;
                            item.use = this.use;
                        }
                    }
                    let params = {
                        message: this.memberRegistTitle+' 정보가 저장 되었습니다'
                    }
                    this.$eventBus.$emit('modalOpen', params);

                }
            })
        }
    },
    mounted() {

    },
    updated() {
        JQ('.quill-content-box .ql-editor').css({'min-height':'500px','max-height':'500px'});

    }

}
</script>

